body {
  font-family: Arial;
}
.layout__wrapper {
  height: 793px;
  position: relative;
  background-color: #fce4d1;
  width: 1122px;
  overflow: hidden;
}
.layout__content {
  position: relative;
  height: 702px;
  top: 68px;
  line-height: 18px;
  margin-left: 44px;
}
.layout__header {
  background-image: url("static/images/layout-header.png");
  height: 69px;
  width: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
  top: 0;
}
.layout__footer {
  height: 22px;
  width: 100%;
  position: absolute;
  bottom: 0;
  background-color: #ea660b;
}
.layout__title {
  margin: 0;
  font-size: 15px;
  text-transform: uppercase;
  color: #ea660b;
  position: absolute;
  top: 13px;
}
.layout__content-button {
  background-color: #e39775;
  display: inline-block;
  border: 2px solid #d98f73;
  text-transform: uppercase;
  color: #fff;
  text-decoration: none;
  border-radius: 4px;
  padding: 3px 3px 0;
  font-size: 13px;
  box-shadow: 1px 1px 1px 0 #e5a386;
  cursor: pointer;
  position: absolute;
  right: 0;
  bottom: 4px;
}
.layout__content-button:hover {
  box-shadow: none;
  right: -1px;
  bottom: 3px;
}
.layout__page-num {
  font-size: 12px;
  color: #ea660b;
  position: absolute;
  bottom: 6px;
  left: 22px;
}
.frame__border,
.frame__container {
  width: 342px;
  height: 704px;
  position: absolute;
  right: 0;
  top: 68px;
}
.frame__border {
  background-image: url("static/images/frame.png");
  background-repeat: no-repeat;
}
.frame__shot {
  width: 286px;
  height: 226px;
  background-image: url("static/images/frame-img.png");
  position: absolute;
  right: 28px;
}
.frame__shot:nth-of-type(1) {
  top: 6px;
}
.frame__shot:nth-of-type(2) {
  top: 238px;
}
.frame__shot:nth-of-type(3) {
  top: 470px;
}
.index__wrapper {
  background-image: url("static/images/index-wrapper.png");
  height: 793px;
  width: 1122px;
  background-size: contain;
  color: #fff;
}
.index__container {
  position: relativa;
  margin-left: 179px;
  padding-top: 186px;
}
.index__title {
  font-size: 21px;
  margin: 0;
  position: absolute;
  text-transform: uppercase;
}
.index__list {
  margin: 52px 0 0 0;
  padding: 0;
  position: absolute;
  list-style-type: none;
}
.index__list-item {
  margin-bottom: 18px;
}
.index__list-marker {
  width: 24px;
  height: 24px;
  background-color: #fdecdf;
  border-radius: 5px;
  position: relative;
  box-shadow: 1px 1px 1px 0 #db531f;
  display: inline-block;
  vertical-align: bottom;
}
.index__list-num {
  color: #f26621;
  font-size: 15px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.index__list-title {
  text-transform: uppercase;
  display: inline-block;
  margin: 0;
  vertical-align: bottom;
  font-size: 16px;
  color: #fff;
  text-decoration: none;
}
.about__content {
  width: 637px;
}
.about__map {
  background-image: url("static/images/map.png");
  width: 424px;
  height: 704px;
  position: absolute;
  right: 0;
  top: 68px;
}
.about__col {
  float: left;
  width: 310px;
  margin: 48px 0 0 17px;
}
.about__col:first-of-type {
  margin-left: 0;
}
.about__col .about__title:first-child {
  margin: 0;
}
.about__title {
  font-size: 13px;
  color: #ea660b;
  text-transform: uppercase;
  margin: 18px 0 0 0;
}
.about__text {
  font-size: 12px;
  margin: 0;
  text-align: justify;
}
